@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Pathway+Extreme:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');
/*  */
html,body{width:100%;height:100%;}
*{box-sizing: border-box;}
.enFont {font-family: 'Pathway Extreme', sans-serif; text-transform: uppercase;}
.ko{font-family:'Pretendard';}
.container{height: inherit; padding: 0 5%; margin: 0 auto;}
.content_container{height: inherit;  padding: 150px 10% 0; margin: 0 auto;}

/*   */
body,div,ul,li,dl,dd,dt,ol,h1,h2,h3,h4,h5,h6,input,fieldset,legend,p,select,table,th,td,tr,textarea,button,form,figure,figcaption{margin:0; padding:0;}

/* a   */
a {text-decoration: none; color: #222222;}

/*   */
body, input, textarea, select, button, table {font-family:'Pretendard','Pathway Extreme', -apple-system, BlinkMacSystemFont, system-ui, Roboto, 'Helvetica Neue', 'Segoe UI', 'Apple SD Gothic Neo', 'Noto Sans KR', 'Malgun Gothic', sans-serif; color: #222222; font-size: 16px; line-height: 1.5;}

/*    */
em,address {font-style: normal;}

/*   */
ul,li,ol {list-style:none;}

/*    */
h1,h2,h3,h4,h5,h6 {font-size: 13px; font-weight: normal;}

/*   */
img,fieldset {border:0 none;}

/*   */
button {border: 0;}

/*  */
video,img {width: 100%;}

/* clearfix */
.cf{*zoom:1;}
.cf:after {display: block; content: ''; clear: both;}


/* IR  */
.ir_pm {display:block; overflow:hidden; font-size:0; line-height:0; text-indent:-9999px;} /*      (Phark Method) */
.ir_wa {display:block; overflow:hidden; position:relative; z-index:-1; width:100%; height: 100%;} /*           (WA IR) */
.ir_so {overflow: hidden; position:absolute; width:0; height:0; line-height:0; text-indent:-9999px;} /*          */

/*     */


@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
       only screen and (min-device-pixel-ratio: 1.5),
       only screen and (min-resolution: 1.5dppx){
          
       }
	   
#skip{z-index:4;position:relative;width:100%;height:auto;font-size:0;}
#skip>li{width:100%;height:auto;font-size:0;}
#skip>li>a{display:block;padding:0;font-size:0;text-align:center;color:#fff;background:#000;}
#skip>li>a:focus{padding:20px;font-size:16px;}








